footer {
  width: 100%;
  height: 110px;
  background: #7fcdd2;
  padding-left: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

footer ul {
  display: flex;
  margin-bottom: 15px;
}

footer ul > li {
  margin-right: 20px;
}
footer a,
footer p {
  font-size: 14px;
}
