@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif,'Noto Sans TC', sans-serif;;
  font-weight: 400;
  font-size: 16px;
  position:relative;
}

.App {
  font-size: 16px;
}

h1 {
  font-size: 26px;
}

h2 {
  font-size: 18px;
}

a {
  color: #000;
  text-decoration: none;
}

li{
  list-style-type: none;;
}
.s1_form {
  width: 990px;
  display: flex;
  flex-direction: column;
  padding-top:65px;
}
.s1_input_t,
.s1_button,
.s1_select {
  height: 70px;
  border-radius: 10px;
  border: 1px solid #fff;
  font-size: 22px;
  padding-left: 65px;
  text-align: left;
  background-color: #fff;
}
.s1_input_t {
  width: 100%;
  margin-bottom: 35px;
}
.s1_input_t,
.s1_select,
.s1_button:focus {
  outline: 0;
}
.s1_select,
.s1_button {
  width: 300px;
  margin: 0 20px 20px 0;
  cursor: pointer;
}
input[type="submit"] {
    margin:24px auto 0px;
    display: block;
    font-size: 30px;
}
.calender {
  width: 320px;
  height: 280px;
  background-color: #fff;
  position: absolute;
  border-radius: 5px;
  margin-top: -5px;
}
.calender::before {
  content: "";
  display: block;
  position: absolute;
  border-right: 10px solid transparent;
  border-left: 10px solid transparent;
  border-bottom: 10px solid white;
  left: 20px;
  top: -10px;
}
.flex {
  display: flex;
}

.section_rh{
    width:100%;
    /* height: calc( 100vh - 162px ); */
    height: 894px;
    background-image: url(/Ray4.0_TaipeiCitizenServicePlatform/static/media/101.5b2bca1c.jpg);
    background-position: top center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.info{
    font-size: 60px;
    margin-bottom:48px;
    color:#FFF;
    font-weight: 700;
}
.s1_search_wrapper{
    background-color:#008992;
    width:1170px;
    height:453px;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    display: flex;
    justify-content: center;
}

.s2_form {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 30px;
}
.s2_input_t,
.s2_button {
  width:247px;
  height: 50px;
  border-radius: 10px;
  border: 2px solid #008992;
  font-size: 16px;
  padding-left: 45px;
  text-align: left;
  background-color: #fff;
  margin-top: 8px;
}
.s2_input_t,
.s2_button:focus {
  outline: 0;
}
.s2_button {
  cursor: pointer;
}
input[type="submit"] {
  margin: 24px auto 0px;
