.s2_container {
  color: #008992;
  height: 75px;
  margin-bottom: 20px;
  font-size: 14px;
}

.filter{
    width: 310px;
    margin-top: 25px;
}
.filter .title{
    font-size: 22px;
    font-weight: 700;
    border: 1px solid #000000;
    border-radius: 5px 5px 0px 0px;
    padding: 16px 0;
    text-align: center;
}
.filter .container{
    padding: 16px 35px;
    border:1px solid #000;
    border-top:none;
}
.filter .list_title{
    font-weight: 700;
    margin-bottom: 10px;
}
.filter .list{
    display: flex;
    flex-direction: column;
}
.filter .list>label{
    padding: 2px;
    display: flex;
    align-items: center;
}
.filter input{
    margin-right: 15px;
    width: 17px;
    height: 17px;
}
* {
}

header{
    height: 81px;
    display: flex;
    align-items: center;
    padding: 0 22px;
    justify-content: space-between;
}
.logo{
    margin-right: 13px;
}
h2 {
  font-weight: 700;
  margin-top:5px
}
h1{
    margin-top:-5px
}
.logo-wrapper {
    display: flex;
}
nav{
    height: 100%;
    display: flex;
}
nav ul{
    display: flex;
    align-items: center;
    height: 100%;
}
nav li{
    display: flex;
    align-items: center;
    height: 100%;

}
nav li>a{
    display: flex;
    align-items: center;
    color:#344344;
    font-weight: 700;
    padding:14px;
    height: 100%;
    transition: all 0.2s;
}
nav li>a:hover{
    color:red;
    transition: all 0.2s;
}

/* title */
.title_wrapper{
    background-color: #7FCDD2;
    height: 81px;
    display: flex;
    padding: 0 35px;
    align-items: center;
}
h3{
    font-size: 26px;
    color: #fff;
}

.button1 {
  padding: 9px 12px;
  font-weight: bold;
  color: #fff;
  background: #00bcc8;
  border: none;
  border-radius: 3px;
  transition: all 0.2s;
}
.button1:hover {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  transition: all 0.2s;
}
.button1:active,
.button1:focus {
  box-shadow: none;
  background: #3a959a;
  transition: all 0.2s;
  outline: none;
}

footer {
  width: 100%;
  height: 110px;
  background: #7fcdd2;
  padding-left: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

footer ul {
  display: flex;
  margin-bottom: 15px;
}

footer ul > li {
  margin-right: 20px;
}
footer a,
footer p {
  font-size: 14px;
}

.rentalresult {
  width: 1170px;
  display: flex;
  justify-content: space-between;
  margin: 26px auto 0;
}
.s2_search_wrapper {
  background-color: #d2edef;
  border: 1px solid #000000;
  width: 310px;
  height: 563px;
  border-radius: 6px;
}
.s2_title {
  height: 70px;
  background-color: #008992;
  color: #fff;
  font-weight: 700;
  font-size: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.rentalresult > .container {
  width: 800px;
}
.countresult {
  font-size: 30px;
  font-weight: 700;
}
.card {
  width: 100%;
  height: 290px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  position: relative;
  display: flex;
  padding: 26px 36px;
}
.card > .district {
  width: 95px;
  height: 50px;
  background: #7fcdd2;
  box-shadow: 3px 4px 9px rgba(0, 0, 0, 0.18);
  color: #fff;
  font-size: 22px;
  font-weight: 700;
  text-shadow: 1px 1px 10px rgba(0, 0, 0, 0.25);
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top:36px;
}
.card > .img{
  width: 300px;
  height: 241px;
  border: 1px solid red;
  margin-right: 26px;
}
.card > .info{
    width: 300px;
    height: 241px;
    color: #000000;
}
.card h4{
    font-weight: bold;
    font-size: 22px;
    margin-bottom: 15px;
}
.card .type{
    background: #7FCDD2;
    width: 48px;
    height: 30px;
    font-size: 14px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    margin-right: 16px;
    margin-bottom: 22px;
}
.card .other{
    margin-bottom: 10px;
    display: flex;
    align-items: center;
}
.card .icon{
    width: 18px;
    height: 18px;
    border: 1px solid red;
    margin-right: 10px;
}
.card .button{
  position: absolute;
  font-size: 22px;
  line-height: 23px;
  right: 26px;
  bottom: 27px;
  transition: all 0.2s;
}
