* {
}

header{
    height: 81px;
    display: flex;
    align-items: center;
    padding: 0 22px;
    justify-content: space-between;
}
.logo{
    margin-right: 13px;
}
h2 {
  font-weight: 700;
  margin-top:5px
}
h1{
    margin-top:-5px
}
.logo-wrapper {
    display: flex;
}
nav{
    height: 100%;
    display: flex;
}
nav ul{
    display: flex;
    align-items: center;
    height: 100%;
}
nav li{
    display: flex;
    align-items: center;
    height: 100%;

}
nav li>a{
    display: flex;
    align-items: center;
    color:#344344;
    font-weight: 700;
    padding:14px;
    height: 100%;
    transition: all 0.2s;
}
nav li>a:hover{
    color:red;
    transition: all 0.2s;
}

/* title */
.title_wrapper{
    background-color: #7FCDD2;
    height: 81px;
    display: flex;
    padding: 0 35px;
    align-items: center;
}
h3{
    font-size: 26px;
    color: #fff;
}
